.alignnone {
	margin: 0;
}

.aligncenter {
	display: block;
	margin: 0 auto;
}

.alignleft {
	float: left;
	margin-right: $sp-single;
}

.alignright {
	float: right;
	margin-left: $sp-single;
}

.alignfull {

	@include full-width;
}

.alignwide {
	width: calc(100% + #{rem(100)});
}



